import React from "react";
import { BoardForm } from "./style/BoardCom_style";

import { Link } from "react-router-dom";

import InfiniteScroll from "react-infinite-scroll-component";
import { Container } from "../../style/Container_style";
import ReactHelmet from "../../../containers/common/ReactHelmet";

const BoardCom = ({
  match,
  posts,
  scroll,
  onClick,
  onChange,
  onSearch,
  fetchMoreData,
  onChangeModal,
}) => {
  return (
    <>
      <ReactHelmet title={`Board`} />
      <Container>
        <title>board</title>
        <BoardForm>
          <div className="boardNav">
            <h1 className="title"></h1>
            <form className="searchbox">
              <select name="name" onChange={onChange}>
                <option value="BO_Title"></option>
                <option value="BO_Writer_NickName"></option>
              </select>
              <input
                className="searchInput"
                type="text"
                name="keyword"
                onChange={onChange}
              />
              <button type="submit" className="searchButton" onClick={onSearch}>
                
              </button>
            </form>
            <button type="button" className="writeButton">
              <Link to={`${match.url}/write`}></Link>
            </button>
          </div>
          <div className="boardForm">
            <InfiniteScroll
              className="infinitescroll"
              dataLength={posts.length}
              next={fetchMoreData}
              hasMore={scroll}
              loader={
                <div className="endBoard">  .</div>
              }
              endMessage={<div className="endBoard">  .</div>}
            >
              <table className="table">
                <thead>
                  <tr>
                    <th className="boardTitle"></th>
                    <th className="boardWriter"></th>
                    <th className="boardDate"></th>
                    <th className="boardHit"></th>
                  </tr>
                </thead>
                <tbody>
                  {posts.map((post, index) => {
                    return (
                      <BoardItem
                        post={post}
                        key={index}
                        onClick={onClick}
                        onChangeModal={onChangeModal}
                      />
                    );
                  })}
                </tbody>
              </table>
            </InfiniteScroll>
          </div>
        </BoardForm>
      </Container>
    </>
  );
};

const BoardItem = ({ post, onClick, onChangeModal }) => {
  const {
    BO_Code,
    BO_Title,
    BO_Creation_Date,
    BO_Writer_NickName,
    BO_Hit,
    BO_State,
  } = post;
  return (
    <>
      <tr
        className="item"
        onClick={() => (BO_State === 0 ? onClick(BO_Code) : onChangeModal())}
      >
        <td className="itemTitle">
          {BO_State === 0 ? BO_Title : " ."}
        </td>
        <td className="itemWriter">{BO_Writer_NickName}</td>
        <td className="itemDate">
          {new Date(BO_Creation_Date).toLocaleDateString()}
        </td>
        <td className="itemHit">{BO_Hit}</td>
      </tr>
      <tr>
        <td
          colSpan="4"
          className="mobileItem"
          onClick={() => (BO_State === 0 ? onClick(BO_Code) : onChangeModal())}
        >
          {BO_State === 0 ? BO_Title : " ."}
          <br />
          {BO_Writer_NickName}&nbsp;&nbsp;{" "}
          {new Date(BO_Creation_Date).toLocaleDateString()}
          &nbsp;&nbsp;  : {BO_Hit}
        </td>
      </tr>
    </>
  );
};

export default BoardCom;
