import React from "react";
import Modal from "./Modal";

const AskModal = ({ visible, title, onConfirm, onCancel }) => {
  if (!visible) return null;
  return (
    <>
      <Modal
        title={title}
        description={` ${title} ?`}
        onConfirm={onConfirm}
        onCancel={onCancel}
        confirmText=""
        cancelText=""
      />
    </>
  );
};
/*          . (Modal)            . */
export default AskModal;
