import React, { useState } from "react";
import AskModal from "./AskModal";
import { EvaluationComForm } from "./style/EvaluationCom_style";

const EvaluationCom = ({
  onHealth,
  onReport,
  healthseeCount,
  reportCount,
  Writer,
}) => {
  const [modal, setModal] = useState(false);
  const [action, setAction] = useState(null);

  const onActionClick = (e) => {
    setModal(true);
    setAction(e.target.name);
  };
  const onCancel = () => {
    setModal(false);
  };
  const onConfirm = () => {
    setModal(false);
    if (action === "") onHealth();
    else onReport();
  };

  return (
    <>
      <EvaluationComForm>
        <div className="contentForm">
          <h3 className="writer"> : {Writer}</h3>
          <table className="buttonForm">
            <thead>
              <tr>
                <th>
                  <button type="submit" name="" onClick={onActionClick}>
                    
                  </button>
                </th>
                <th>
                  <button type="submit" name="" onClick={onActionClick}>
                    
                  </button>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>{healthseeCount}</th>
                <th> {reportCount}</th>
              </tr>
            </tbody>
          </table>
        </div>
      </EvaluationComForm>
      <AskModal
        visible={modal}
        title={action}
        onConfirm={onConfirm}
        onCancel={onCancel}
      />
    </>
  );
};

export default EvaluationCom;
