import React from "react";
import { Container } from "../../style/Container_style";
import { Link } from "react-router-dom";
import { AlbumWriteForm } from "./style/AlbumWrite_style";
import ImageGallery from "react-image-gallery";
import { GalleryWrite } from "./style/AlbumWriteGallery_style";

const AlbumWrite = ({ match, onChange, field }) => {
  const { content, scope, img } = field;
  return (
    <>
      <Container>
        {console.log(content)}
        <AlbumWriteForm>
          {/*  */}
          <div className="titleBar">
            <button className="backButton">
              <Link to={`/Album/${match}`}></Link>
            </button>
            <button className="addButton"></button>
          </div>
          {/*    */}
          <div className="fileForm">
            {/*    */}
            <div className="fileButton">
              <label className="inputImg" htmlFor="file">
                  &nbsp;
              </label>
              <label className="fileInput" htmlFor="file">
                &#8626;
              </label>
              <input
                className="hidden"
                id="file"
                type="file"
                name="file"
                multiple
                onChange={onChange}
              />
            </div>

            {/*      */}
            <dl>
              <dt> </dt>
              <div className="filenameform">
                <dd>  </dd>
              </div>
            </dl>
          </div>

          <div className="context">
            <div className="imageView">
              <GalleryWrite>
                <ImageGallery
                  thumbnailPosition={"bottom"}
                  showPlayButton={false}
                  showFullscreenButton={false}
                  items={img}
                />
              </GalleryWrite>
            </div>

            {/*    */}
            <textarea
              className="contentDiv"
              type="text"
              value={content}
              onChange={onChange}
            />
          </div>
        </AlbumWriteForm>
      </Container>
    </>
  );
};

export default AlbumWrite;
