import React from "react";
import { Container } from "../style/RegisterBOCom_style";
import { Link } from "react-router-dom";

const RegisterBO = ({ onClick, onChange, onSearch }) => {
  return (
    <Container>
      <div className="Board">
        <div className="Title">
           
          <Link to={`/Mypage/registerBO/write`}></Link>
        </div>
        <div className="BoardForm">
          <div className="flex">
            <div className="BoardTitle"></div>
            <div className="BoardDate"></div>
            <div className="BoardResult"></div>
          </div>
          <BoardItem></BoardItem>
        </div>
      </div>
    </Container>
  );
};

const BoardItem = () => {
  return (
    <>
      <Link to={`/Mypage/registerBO/read`}>
        <div className="Item">
          <div className="ItemTitle"></div>
          <div className="ItemDate">2020-07-02</div>
          <div className="ItemResult"></div>
        </div>
      </Link>
    </>
  );
};

export default RegisterBO;
